import $http from '../../common/api/request.js'
import Card from '@/components/common/Card.vue'
import CommodityList from '@/components/common/CommodityList.vue'
import UniNumberBox from '@/components/uni/uni-number-box/uni-number-box.vue'
import {mapMutations} from 'vuex'
export default {
	components: {
		Card,
		CommodityList,
		UniNumberBox
	},
	data() {
		return {
			// 设置默认的数量
			num:1,
			// 数据库中获取的数据
			detailsData: {},
			isShow: false,
			// 定义弹框的时间对象
			animationData: {},
			// 详情轮播区域
			swiperList: [{
					imgUrl: '../../static/img/details1.jpeg'
				},
				{
					imgUrl: '../../static/img/details2.jpeg'
				},
				{
					imgUrl: '../../static/img/details3.jpeg'
				}
			],
			// 商品展示的区域
			commodData: [{
					id: '1',
					imgUrl: "../../static/img/commodity1.jpg",
					name: "大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008",
					pprice: "299",
					oprice: "659",
					discount: "5.2"
				},
				{
					id: '2',
					imgUrl: "../../static/img/commodity2.jpg",
					name: "大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008",
					pprice: "299",
					oprice: "659",
					discount: "5.2"
				},
				{
					id: '3',
					imgUrl: "../../static/img/commodity3.jpg",
					name: "大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008",
					pprice: "299",
					oprice: "659",
					discount: "5.2"
				},
				{
					id: '4',
					imgUrl: "../../static/img/commodity4.jpg",
					name: "大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008",
					pprice: "299",
					oprice: "659",
					discount: "5.2"
				}
			]
		};
	},
	// 监听头部返回首页的默认行为
	onBackPress() {
		if (this.isShow) {
			this.hideShow()
			return true
		}
	},
	onLoad(e) {
		this.getDetailsData(e.id)
	},
	// 分享功能实现
	onNavigationBarButtonTap(e) {
		if(e.type === "share") {
			uni.share({
				"provider":"weixin",
				"type":0,
				"title":this.detailsData.name,
				"scene":"WXSceneSession",
				"href":`http://192.168.1.5:8080/#/pages/details/details?id=${this.detailsData.id}`,
				imageUrl:this.detailsData.imgUrl,
				success(res) {
					uni.showTabBar({
						title:'分享成功'
					})
				},
				fail(err) {
					console.log("fail" + JSON.stringify(err))
				}
			})
		}
	},
	methods: {
		// 请求数据
		getDetailsData(id) {
			$http.request({
				url: "/goods/id",
				data: {
					id: id
				}
			}).then(res => {
				this.detailsData = res[0]
			}).catch(() => {
				uni.showToast({
					title: "请求出错",
					icon: "none"
				})
			})
		},
		// 显示蒙层
		toShow() {

			var animation = uni.createAnimation({
				duration: 200
			})

			animation.translateY(600).step()
			this.animationData = animation.export()

			this.isShow = true

			setTimeout(() => {
				animation.translateY(0).step()
				this.animationData = animation.export()
			}, 200)
		},
		// 隐藏蒙层
		hideShow() {
			var animation = uni.createAnimation({
				duration: 200
			})

			animation.translateY(600).step()
			this.animationData = animation.export()

			this.isShow = true

			setTimeout(() => {
				animation.translateY(0).step()
				this.isShow = false
			}, 200)

		},
		// 监听数量的变化
		changeIndex(value){
			this.num = value
		},
		// 点击进入购物车页面
		toShopCart(){
			uni.navigateTo({
				url:'../ShopCart/ShopCart'
			})
	
		},
		// 加入购物车
		addShopcar(){
			let goods = this.detailsData
			this.detailsData['checked'] = false
			this.detailsData['num'] = this.num
			// 加入购物车
			this.addcar(goods)
			// 隐藏底部蒙层
			this.hideShow()
			uni.showToast({
				title:"加入购物车成功",
				icon:"none"
			})
		},
		...mapMutations(['addcar'])
	}
}
